.label {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
    color: #757575;
}

.button {
    min-width: 200px;
    height: 50px;
    font-family: inherit;
    background-color: #F6AE2D;
    border: none;
    border-radius: 4px;
    color: #000807;

    font-size: 20px;
}

.buttonDiv {
    display: flex;
    justify-content: center;
}

.input {
    height: 40px;
    outline: none;
    border: 1px solid rgba(33, 33, 33, 0.2);
    cursor: pointer;
    border-radius: 4px;
    font-size: 15px;
    color: #757575;
    font-family: inherit;

    padding-left: 12px;
}