.contact {
    margin: 0 auto 15px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #757575;
}

.name {
    min-width: 175px;
    margin-right: 10px;
}

.number {
    min-width: 120px;
    margin-right: 10px;
}

.button {
    width: 56px;
    height: 25px;
    font-family: inherit;
    background-color: #F6AE2D;
    border: none;
    border-radius: 4px;
    color: #000807;
    font-size: 15px;

    display: flex;
    text-align: center;
    justify-content: center;
}